import Axios, {
  AxiosRequestConfig,
  AxiosResponse,
  AxiosError,
  AxiosInstance,
} from "axios";
import Qs from "qs";
import { ElMessage } from "element-plus";

const message = (msg: string, type?: any) => {
  ElMessage({
    message: msg,
    type: type || "warning",
    duration: 1500,
  });
};

class EnclosureHttp {
  constructor() {
    this.httpInterceptorsRequest();
    this.httpInterceptorsResponse();
  }

  // Axios 
  private static axiosInstance: AxiosInstance = Axios.create({
    baseURL: "https://autumnfish.cn/",
    timeout: 10000, //10
    headers: {
      Accept: "application/json, text/plain, */*",
      "Content-Type": "application/json",
      "X-Requested-With": "XMLHttpRequest",
    },
  });

  //
  private httpInterceptorsRequest(): void {
    const instance = EnclosureHttp.axiosInstance;
    instance.interceptors.request.use(
      (config: AxiosRequestConfig) => {
        /*
         * 
         * */
        // if (process.env.NODE_ENV === "production") {
        config.url = config.url.substr(3);
        // }

        return config;
      },
      (err) => {
        return Promise.reject(err);
      }
    );
  }

  //
  private httpInterceptorsResponse(): void {
    const instance = EnclosureHttp.axiosInstance;
    instance.interceptors.response.use(
      (response: AxiosResponse) => {
        /*
         *   
         * */
        response.status === 200
          ? Promise.resolve(response)
          : Promise.reject(response);
      },
      (error: AxiosError) => {
        //
        const { response } = error;
        if (response) {
          // 2xx
          this.errorHandle(response.status, response.data.message);
          return Promise.reject(response);
        } else {
          // 
          // eg:statenetwork
          // networkapp.vue
          // 
          return Promise.reject(error);
        }
      }
    );
  }

  /**
   * 
   * @param {Number} status 
   */
  private errorHandle = (status: number, other: string) => {
    // 
    switch (status) {
      case -1: // -1: 
        message("");
        break;
      case 403: // 403 token
        message("");
        break;
      case 404: // 404
        message("");
        break;
      default:
        message(other);
    }
  };

  /**
   * get
   * @param {string} url 
   * @param {object} params 
   */
  public get = (url: string, params?: unknown) => {
    return new Promise((resolve, reject) => {
      Axios.get(url, { params }).then(
        (res) => {
          resolve(res);
        },
        (err) => {
          reject(err);
        }
      );
    });
  };

  /**
   * post 
   * @param {string} url 
   * @param {object} params 
   */
  public post = (url: string, params: unknown = {}) => {
    return new Promise((resolve, reject) => {
      Axios.post(url, Qs.stringify(params))
        .then((res) => {
          resolve(res);
        })
        .catch((err) => {
          reject(err);
        });
    });
  };
}

export default EnclosureHttp;
